/**
 * Get stored country
 * @returns {string} country
 */
export function getCountry() {
  return getData('country');
}

/**
 * Get stored currency
 * @returns {string} currency
 */
export function getCurrency() {
  return getData('currency');
}

/**
 * Filter currencies to those allowed by a specific country
 * @param {string} country - baseCountry for the client
 * @param {object} clientConfig - available country and currency configs for a client
 * @param {array} clientConfig.countries - allowed countries for the client
 * @param {array} clientConfig.currencies - allowed currencies for the client
 * @returns {array} - list of currencies allowed for country for this client
 */
export function getCurrenciesForCountry(country, {countries, currencies}) {
  const countryData = countries.find(a => a.code === country);
  if(countryData.requireDefaultCurrency === true) {
    return currencies.filter(a => a.code === countryData.defaultCurrency);
  } else {
    return currencies;
  }
}

/**
 * Returns true if currency is supported for a country
 * @param {string} country - baseCountry for the client
 * @param {object} clientConfig - available country and currency configs for a client
 * @param {array} clientConfig.countries - allowed countries for the client
 * @param {array} clientConfig.currencies - allowed currencies for the client
 * @returns {boolean}
 */
export function isCurrencySupportedForCountry(currency, country, {countries, currencies}) {
  try {
    const countryData = countries.find(a => a.code === country);
    const currencySupportedByClient = currencies.find(a => a.code === currency);
    const currencyIsDefaultCurrencyForCountry = (currency === countryData.defaultCurrency);

    if(currencySupportedByClient) {
      if(!countryData.requireDefaultCurrency) {
        return true;
      } else {
        return currencyIsDefaultCurrencyForCountry;
      }
    } else {
      return false;
    }
  } catch {
    return false;
  }
}
/**
 * Returns the country object from countries by country code
 * @param {string} country
 * @param {array.<Object>} countries
 * @returns {object} country object
 */
export function getCountryFromCountries(country, countries) {
  return countries.find(a => a.code === country);
}

/**
 * Returns true if country is supported by client's country list
 * @param {string} country
 * @param {array} countries
 * @returns {boolean}
 */
export function isCountrySupported(country, countries) {
  return typeof getCountryFromCountries(country, countries) !== 'undefined';
}


/**
 * Gets the default country to be used in the currency selector
 * @param {string} baseCountry - base country from API
 * @param {string} clientConfigCountry - default country from client data attributes
 * @param {array} countries - array of countries supported by client from API
 * @returns {string} country
 */
export function getDefaultCountry(baseCountry, clientConfigCountry, countries) {
  if(isCountrySupported(clientConfigCountry, countries)) {
    return clientConfigCountry;
  } else {
    const browserCountry =  navigator.language.replace('-', '_').split('_')[1].toUpperCase();
    const browserCountryIsSupported = isCountrySupported(browserCountry, countries);
    return browserCountryIsSupported ? browserCountry : baseCountry;
  }
}


/**
 * Sets data in local storage and adds it to configuration
 * @param {string} key
 * @param {*} value
 * @param {object} configuration
 */
export function setData(key, value, configuration) {
  const cookieName = `dr-${key}`;
  document.cookie = `${cookieName}=${value}; SameSite=None; path=/; Secure`;
  configuration[key] = value;
}

/**
 * Gets data from local storage
 * @param {string} key
 * @returns {*} value
 */
export function getData(key) {
  if(document.cookie.indexOf(`dr-${key}=`) === -1) {
    return;
  }

  const cookieValue = document.cookie
    .split('; ')
    .find(row => row.startsWith(`dr-${key}=`))
    .split('=')[1];
  return cookieValue;
}

/**
 * Creates a dom element
 * @param {string} type
 * @param {string} id
 * @param {string} innerHTML
 * @param {string} style
 * @returns {HTMLElement}
 */
export function createDomElement(type, id, innerHTML, style) {
  const el = document.createElement(type);
  if (id) el.id = id;
  if(innerHTML) el.innerHTML = innerHTML;
  if (style) el.style = style;
  return el;
}

/**
 * Appends a child element to a parent element
 * @param {string} parentId
 * @param {HTMLElement} child
 */
export function mountDomElement(parentId, child) {
  const isElementLoaded = async elId => {
    while ( document.getElementById(elId) === null) {
      await new Promise( resolve =>  requestAnimationFrame(resolve) );
    }
    return document.getElementById(elId);
  };

  isElementLoaded(parentId).then(() => {
    document.getElementById(parentId).appendChild(child);
  });
}

/**
 * Formats a string to a given pattern
 * @example
 *  // returns '1,234.56'
 *  formatNumber('#,###.##', 1234.56);
 * @param {string} pattern
 * @param {number} number
 * @returns {string} string formatted to pattern
 */
function formatNumber(pattern, number) {
  pattern = pattern.trim();
  let formattedNumber;
  const separators = pattern.match(/[^#]/g);
  const decimal = separators[separators.length-1];
  const groupSeparator = separators[0];
  const groupLengths = pattern.split(decimal)[0].split(groupSeparator).slice(1).map(a => a.length);
  const groupLength = pattern.split(decimal)[0].split(groupSeparator).pop().length;
  if(groupLengths.length === 1) {
    const integerRegex = new RegExp(/^[+-]?\d+/);
    const groupingRegexPattern = `(\\d)(?=(\\d{${groupLength}})+$)`;
    const groupingRegex = new RegExp(groupingRegexPattern, 'g');
    formattedNumber =  number.toString().replace('.', decimal).replace(integerRegex, function(int) {
      return int.replace(groupingRegex, `$1${groupSeparator}`);
    });
  } else { //this is for multiple grouping lengths, like India #,##,###.##
    const lastGroupLength = groupLengths[groupLengths.length - 1];
    const firstGroupLength = groupLengths[0];
    const decimals =  number.toString().split('.')[1] || '';
    const formattedDecimal = decimals ? `${decimal}${decimals}` : '';
    let integers = number.toString().split('.')[0];
    let lastGroup = integers.substring(integers.length-lastGroupLength);
    const otherNumbers = integers.substring(0,integers.length-lastGroupLength);
    const regex = new RegExp(`\\B(?=(\\d{${firstGroupLength}})+(?!\\d))`, 'g');

    lastGroup = otherNumbers != '' ? `,${lastGroup}` : lastGroup;
    const formattedIntegers = otherNumbers.replace(regex, ',') + lastGroup;
    formattedNumber = `${formattedIntegers}${formattedDecimal}`;
  }

  return formattedNumber;
}

/**
 * DEPRECATED...but leaving it here in case something changes and we need it again
 * Truncates a price to a certain number of decimal places
 * @param {number} price
 * @param {number} decimalPlaces
 * @returns {string} truncated price
 */
export function truncatePrice(price, decimalPlaces) {
  const priceString = price.toString();
  if(priceString.indexOf('.') !== -1 && decimalPlaces !== 0) {
    const end = priceString.indexOf('.') + decimalPlaces + 1;
    return priceString.substring(0, end);
  } else {
    return `${priceString}.${'0'.repeat(decimalPlaces)}`; //we can't use toFixed here because it rounds. For mvp we are truncating, not rounding
  }
}

/**
 * Formats a price for international prices according to settings in config
 * @param {number} price
 * @param {object} priceConfig
 * @returns {string} price formatted correctly for region
 */
export function formatPrice(price, priceConfig) {
  const { currencyNumberFormat, currencySymbolBeforePrice, symbol, useCurrencySymbolSpace} = priceConfig.pricingFormat;
  const formattedNumber = formatNumber(currencyNumberFormat, price);
  return `${currencySymbolBeforePrice ? symbol : ''}${currencySymbolBeforePrice && useCurrencySymbolSpace ? ' ' : ''}${formattedNumber}${!currencySymbolBeforePrice && useCurrencySymbolSpace ? ' ' : ''}${!currencySymbolBeforePrice ? symbol : ''}`;
}

/**
 * handles event when user clicks on modal, closes the modal
 * @param event
 */
export function handleModalClick(event) {
  if(event.target.id === 'DR-currency-selector-modal') {
    handleModalClose();
  }
}

export function handleModalClose() {
  openModal(false);
}

/**
 * updates css for modal to show/hide
 * @param {boolean} modalShouldBeOpen
 */
export function openModal(modalShouldBeOpen) {
  if(modalShouldBeOpen) {
    document.getElementById('DR-currency-selector-modal').style.display = 'flex';
    document.body.classList.add('DR-currency-selector-modal-open');
  } else {
    document.getElementById('DR-currency-selector-modal').style.display = 'none';
    document.body.classList.remove('DR-currency-selector-modal-open');
  }
}

/**
 *
 * @param {array} mutations
 * @param handleUpdatePrices
 * @param {object} configuration
 */
export function listenForNewPrices(mutations, handleUpdatePrices, handleUpdatedOriginalPrices, configuration) {
  const newPriceElements = mutations.filter(mutation => isNewPriceElement(mutation, configuration)).map(mutation => mutation.target);
  const newOriginalPrices = mutations.filter(mutation => isNewOriginalPrice(mutation)).map(mutation => mutation.target);

  if(newPriceElements.length > 0) {
    handleUpdatePrices(getCountry(), getCurrency(), newPriceElements);
  } else if(newOriginalPrices.length > 0) {
    handleUpdatedOriginalPrices(getCountry(), getCurrency(), configuration, newOriginalPrices);
  }
}

/**
 *
 * @param {MutationRecord} mutation
 * @param {object} configuration
 * @returns {boolean}
 */
function isNewPriceElement(mutation, configuration) {
  return !isMutationTargetPartOfSelector(mutation, configuration) && (mutation.type === 'childList' && mutation.addedNodes.length > 0);
}

/**
 *
 * @param {MutationRecord} mutation
 * @returns {boolean}
 */
function isNewOriginalPrice(mutation) {
  return mutation.type === 'attributes' && mutation.target.hasAttribute('data-dr-original-price') && mutation.target.getAttribute('data-dr-original-price').value !== mutation.oldValue;
}

/**
 *
 * @param {object} mutation
 * @param {object} configuration
 * @returns {boolean}
 */
function isMutationTargetPartOfSelector(mutation, configuration) {
  const selectorElementIds = [
    'DR-currency-selector-selected-currency',
    'DR-currency-selector-currency',
    'DR-currency-selector-country',
    'DR-currencySelector',
    configuration.currencySelectorElement
  ];
  return selectorElementIds.includes(mutation.target.id);
}

/**
 *
 * @param {string} originalPrice
 * @param {object} priceConfig
 * @returns {number}
 */
export function getCalculatedPrice(originalPrice, priceConfig) {
  const formattedOriginalPrice = parseInt(originalPrice).toFixed(priceConfig.pricingFormat.decimalPlaces);
  if(priceConfig.operatedByDr === false || priceConfig.supportsFixedPrices === true) {
    return formattedOriginalPrice;
  } else {
    const applyPricingRules = new Function( `return ${priceConfig.pricingRuleFunction}`)();
    const {conversionFactor} = priceConfig;
    const newPrice = +originalPrice * conversionFactor;
    return applyPricingRules(newPrice);
  }

}

/**
 *
 * @param {string} apiKey
 * @returns {Promise<Response>}
 */
export function getClientConfig(apiKey) {
  return fetch('https://api.digitalriver.com/dynamic-pricing/country-currency/', {
    headers: {
      Authorization: `bearer ${apiKey}`
    }
  });
}

/**
 *
 * @param {string} apiKey
 * @param {object} configuration
 * @returns {Promise<Response>}
 */
export function createAndMountCurrencySelector(apiKey,  configuration, createCurrencySelector, addPriceObserver) {
  return getClientConfig(apiKey).then(async result => {
    const resultData = await result.json();
    const {currencySelectorElement, clientConfigDefaultCountry} = configuration;
    const {baseCountry, countries} = resultData;
    let country = getCountry();
    let currency = getCurrency();

    if(!isCountrySupported(country, countries)) {
      country = getDefaultCountry(baseCountry, clientConfigDefaultCountry, countries);
      setData('country', country, configuration);
    }

    const currencyIsSupported = isCurrencySupportedForCountry(currency, configuration.selectedCountry, resultData);
    if(!currencyIsSupported) {
      const countryData = getCountryFromCountries(country, countries);
      currency = countryData.defaultCurrency;
      setData('currency', currency, configuration);
    }

    configuration.clientConfig = resultData;
    const currencySelector = createCurrencySelector();
    mountDomElement(currencySelectorElement, currencySelector);
    //listen for future products to be added
    addPriceObserver();
  }).catch(() => {
    throw new Error('There has been an error with the Digital River APIs. Please try again.');
  });
}

/**
 *
 * @param {HTMLElement} priceEl
 * @param {string} price
 * @param {object} priceConfig
 */
export function updatePriceElDataAttributes(priceEl, price, priceConfig) {
  const {currencyCode, countryCode} = priceConfig;
  priceEl.setAttribute('data-dr-currency', currencyCode);
  priceEl.setAttribute('data-dr-country', countryCode);
  priceEl.setAttribute('data-dr-converted-price', price);
  priceEl.setAttribute('data-dr-formatted-price', formatPrice(price, priceConfig));
  priceEl.innerHTML = priceEl.getAttribute('data-DR-formatted-price');
}

/**
 *
 * @param {string} country
 * @param {string} currency
 * @param {object} cconfiguration
 * @param {HTMLElement[]} elements
 */
export function handleUpdatedOriginalPrices(country, currency, configuration, elements) {
  const countryCurrencyCombo = `${country}-${currency}`;
  const priceConfigData = configuration.priceConfig[countryCurrencyCombo];
  elements.forEach(priceEl => {
    const originalPrice = priceEl.getAttribute('data-dr-original-price');
    const convertedPrice = getCalculatedPrice(originalPrice, priceConfigData);
    updatePriceElDataAttributes(priceEl, convertedPrice, priceConfigData);
  });
}