import {
  createAndMountCurrencySelector,
  createDomElement,
  getCountry,
  getCalculatedPrice,
  getCurrenciesForCountry,
  getCurrency,
  handleModalClick,
  handleUpdatedOriginalPrices,
  openModal,
  listenForNewPrices,
  updatePriceElDataAttributes,
  setData, handleModalClose
} from './dynamic-pricing-utils';



(function() {
  const script = document.currentScript || (function() {
    const scripts = document.getElementsByTagName('script');
    return scripts[scripts.length - 1];
  })();


  if(!script.getAttribute('data-dr-apiKey')) {
    throw new Error('Please verify that your DR-Dynamic-Pricing script element contains a data-dr-apiKey attribute containing your public API key as the value');
  }

  let configuration = {
    priceElementSelector: '[data-dr-original-price]',
    currencySelectorElement: script.getAttribute('data-dr-currency-selector') || 'DR-currencySelector',
    clientConfigDefaultCountry: script.getAttribute('data-dr-default-country'),
    apiKey: script.getAttribute('data-dr-apiKey'),
    selectedCountry: getCountry(),
    selectedCurrency: getCurrency(),
    priceConfig: {}
  };
  window.digitalRiverPricing = new DynamicPricing(configuration);
})();



export default function DynamicPricing(configuration) {
  const {apiKey} = configuration;
  this.getCountry = getCountry;
  this.getCurrency = getCurrency;
  const FLAG_URL = 'https://ui1.img.digitalrivercontent.net/Storefront/images/flags/';


  createAndMountCurrencySelector(apiKey, configuration, createCurrencySelector, addPriceObserver);



  function getPricingConfig(country, currency) {
    return fetch(`https://api.digitalriver.com/dynamic-pricing/country-currency/countries/${country}/currencies/${currency}`, {
      headers: {
        Authorization: `bearer ${apiKey}`
      }
    });
  }

  function createCurrencySelector() {
    const config = configuration.clientConfig;
    const {countries} = config;
    const country = getCountry();
    const currency = getCurrency();
    const currencySelectorEl = createDomElement('div', 'DR-currency-selector');
    currencySelectorEl.addEventListener('click', () => openModal(true));

    const selectedCountryEl = createDomElement('img', 'DR-currency-selector-selected-country');
    selectedCountryEl.alt = country.toUpperCase();
    selectedCountryEl.src = `${FLAG_URL}${country.toLowerCase()}.svg`;
    selectedCountryEl.width = '30';
    currencySelectorEl.appendChild(selectedCountryEl);

    const selectedCurrencyEl = createDomElement('span', 'DR-currency-selector-selected-currency', currency);
    currencySelectorEl.appendChild(selectedCurrencyEl);


    // modal
    const currencySelectorModal = createDomElement('div', 'DR-currency-selector-modal', '', 'display:none');
    currencySelectorModal.addEventListener('click', handleModalClick);
    const currencySelectorModalContent = createDomElement('div', 'DR-currency-selector-modal-content');

    const countryLabel = createDomElement('label', 'DR-country-label', 'Change your country');
    const countrySelect = createDomElement('select', 'DR-currency-selector-country');
    //add country options
    createAndAppendOptionsFromArray(countries, 'code', 'displayName', countrySelect, getCountry());
    countrySelect.addEventListener('change', handleCountryChange);

    currencySelectorModalContent.appendChild(countryLabel);
    currencySelectorModalContent.appendChild(countrySelect);

    const currencyLabel = createDomElement('label', 'DR-currency-label', 'Change your currency');
    const currencySelect = createDomElement('select', 'DR-currency-selector-currency');
    //add currency options
    createAndAppendOptionsFromArray(getCurrenciesForCountry(country, config), 'code', 'displayName', currencySelect, currency);

    currencySelectorModalContent.appendChild(currencyLabel);
    currencySelectorModalContent.appendChild(currencySelect);

    const saveButton = createDomElement('button', 'DR-currency-selector-save', 'save');
    currencySelectorModalContent.appendChild(saveButton);

    const cancelButton = createDomElement('button', 'DR-currency-selector-cancel', 'cancel');
    cancelButton.addEventListener('click', handleModalClose);
    currencySelectorModalContent.appendChild(cancelButton);

    currencySelectorModal.appendChild(currencySelectorModalContent);

    document.body.appendChild(currencySelectorModal);

    document.getElementById('DR-currency-selector-save').addEventListener('click', () => {
      const currency = document.getElementById('DR-currency-selector-currency').value;
      const country = document.getElementById('DR-currency-selector-country').value;
      handleUpdatePrices(country, currency);
      updateCurrencySelector(country,currency);
      handleModalClose();
    });

    handleUpdatePrices(country, currency);
    return currencySelectorEl;
  }



  async function handleUpdatePrices(country, currency) {
    const countryCurrencyCombo = `${country}-${currency}`;
    if(!configuration.priceConfig[countryCurrencyCombo]) {
      const priceConfig = await getPricingConfig(country, currency);
      configuration.priceConfig[countryCurrencyCombo] = await priceConfig.json();
    }

    const priceConfigData = configuration.priceConfig[countryCurrencyCombo];
    const currentCurrency = getCurrency();
    const priceElements = Array.from(document.querySelectorAll(configuration.priceElementSelector));
    priceElements.forEach((priceEl) => {
      const originalPrice = priceEl.getAttribute('data-dr-original-price');
      if(currency !== currentCurrency) {
        // changed currency, update all
        const convertedPrice = getCalculatedPrice(originalPrice, priceConfigData);
        updatePriceElDataAttributes(priceEl, convertedPrice, priceConfigData);
      } else if (!priceEl.getAttribute('data-dr-converted-price')) {
        // only update new products
        const convertedPrice = getCalculatedPrice(originalPrice, priceConfigData);
        updatePriceElDataAttributes(priceEl, convertedPrice, priceConfigData);
      }

    });
    setData('currency', currency, configuration);
    setData('country', country, configuration);
  }

  function updateCurrencySelector(country,currency) {
    const selectedCurrencyEl = document.getElementById('DR-currency-selector-selected-currency');
    selectedCurrencyEl.innerHTML = currency;
    const selectedCountryEl = document.getElementById('DR-currency-selector-selected-country');
    selectedCountryEl.alt = country.toUpperCase();
    selectedCountryEl.src = `${FLAG_URL}${country.toLowerCase()}.svg`;
  }



  function addPriceObserver() {
    const drPriceObserver = new MutationObserver(listen);
    drPriceObserver.observe(document.body, { subtree: true, childList: true, attributes: true, attributeOldValue : true});
  }

  function listen(mutations) {
    listenForNewPrices(mutations, handleUpdatePrices, handleUpdatedOriginalPrices, configuration);
  }

  function createAndAppendOptionsFromArray(array, valueProperty, textProperty, parent, selectedValue) {
    parent.innerHTML = '';
    array.forEach(item => {
      const option = createDomElement('option', null, item[textProperty]);
      option.value = item[valueProperty];
      if(item[valueProperty] === selectedValue) {
        option.setAttribute('selected', 'selected');
      }
      parent.appendChild(option);
    });
  }

  function handleCountryChange(event) {
    const config = configuration.clientConfig;
    const country = event.target.value;
    const configCountryData = config.countries.find(a => a.code === country);
    const {defaultCurrency} = configCountryData;
    const currencySelector = document.getElementById('DR-currency-selector-currency');
    const currenciesForCountry = getCurrenciesForCountry(country, config);
    createAndAppendOptionsFromArray(currenciesForCountry, 'code', 'displayName', currencySelector, defaultCurrency);
  }



}
